.about {
    position: relative;
    display: flex;
    padding: 220px 200px 0 200px;
    background: linear-gradient(0deg, rgba(35,47,66, 0.95), rgba(35,47,66, 0.9)),
    url('https://img2.freepng.ru/20180328/fte/kisspng-cherry-blossom-flower-clip-art-blossom-5abbbb92cb0f86.0531277415222526908317.jpg') 0/contain repeat;
}

.about img {
    /*display: block;*/
    position: absolute;
    bottom: -80px;
    justify-self: center;
    width: 200px;
    height: 200px;
    margin-right: 20px;
    border-radius: 100%;
    border: 7px solid #ffffff;
}

.descr{
margin-left: 250px;
}

.descr .name {
    color: #fe61c1;
    font-size: 36px;
    font-weight: 700;
    letter-spacing: 1px;
    font-family: 'Dancing Script', sans-serif;
}

.descr p {
    color: rgb(216, 236, 229);
    margin: 7px;
}
.input{
   position: relative;
    top: -80px;
}
.contact {
    padding-left: 10px;
}