.title {
    margin: 20px auto 20px 50px;
    color: rgb(53, 114, 92);
    font-size: 18px;
    font-weight: 400;
}

.wall p {
    display: flex;
    margin: 20px auto;
    color: rgb(26, 229, 158);
    font-size: 26px;
    font-weight: 500;
    text-transform: uppercase;
    justify-content: center;
}

.posts {

}

.area {
    outline: none;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    color: azure;
}

.submitButton {
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 3px;
    outline: none;
    margin: 10px;
    border: none;
    color: white;
    font-weight: 700;
    font-size: 14px;
    background-color: coral;
}