.message {
    max-width: 1200px;
    margin: 0 auto;
}

.post img {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    margin-right: 10px;
}

.post {
    display: flex;
    margin: 20px auto 20px 20px;
    color: rgb(216, 236, 229);
    font-size: 18px;
}

.text{
    min-width: 100%;
    padding: 10px;
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 10px 10px 10px 0px;
    position: relative;
}
.text:before{
    content: "";
    position: absolute;
    left: -20px;
    bottom: 0;
    display: block;
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-right: 20px solid rgba(0, 0, 0, 0.4);
    border-bottom: 7px solid transparent;
}
.marks {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: rgb(216, 236, 229);
    font-size: 18px;
}

.like {
    display: inline-block;
    background: url("https://www.searchpng.com/wp-content/uploads/2019/02/Instagram-Like-Icon.png") left no-repeat;
    background-size: contain;
    width: 20px;
    height: 27px;
    padding: 0 30px;
}

.dislike {
    display: inline-block;
    background: url("https://img.pngio.com/gauge-and-icon-overview-conduent-healthy-communities-institute-bad-png-500_500.png") left no-repeat;
    background-size: contain;
    width: 20px;
    height: 27px;
    padding: 0 30px;
}